<template>
  <section class="section section-shaped section-lg my-0">
    <div class="shape shape-style-1 bg-gradient-default">
    </div>
    <div class="container pt-lg-md">
      <div class="row justify-content-center">
        <div class="col-lg-5">
          <card type="secondary" shadow header-classes="bg-white pb-6" body-classes="px-lg-6 py-lg-6" class="border-0">
            <template>
              <form role="form" @submit.prevent="onSubmit()">
                Você é ou representa uma empresa?
                <div class="custom-control custom-radio mt-3">
                  <input class="custom-control-input" type="radio" id="radio1" v-bind:value="true" v-model="user.is_company">
                  <label class="custom-control-label" for="radio1">Sim</label>
                </div>
                <div class="custom-control custom-radio mb-3">
                  <input class="custom-control-input" type="radio" id="radio2" v-bind:value="false" v-model="user.is_company">
                  <label class="custom-control-label" for="radio2">Não</label>
                </div>
                <base-input alternative class="mb-3" placeholder="Nome" addon-left-icon="ni ni-hat-3"
                  required v-model="user.name" :valid="$v.user.name.required && $v.user.name.minLength" :error="errorName" v-on:blur="dirtyName">
                </base-input>
                <base-input v-if="!user.is_company" alternative class="mb-3" placeholder="Sobrenome" addon-left-icon="ni ni-hat-3"
                  required v-model="user.last_name" :valid="$v.user.last_name.minLength && $v.user.last_name.required" :error="errorLastName" v-on:blur="dirtyLastName">
                </base-input>
                <base-input v-if="user.is_company" alternative class="mb-3" placeholder="Site" addon-left-icon="ni ni-hat-3"
                  required v-model="user.site" :valid="$v.user.site.minLength" :error="errorSite" v-on:blur="dirtySite">
                </base-input>
                <base-input alternative class="mb-3" placeholder="Email" addon-left-icon="ni ni-email-83"
                  required v-model="user.email" :valid="$v.user.email.required && $v.user.email.email" :error="errorEmail" v-on:blur="dirtyEmail">
                </base-input>
                <base-input alternative type="password" placeholder="Senha" addon-left-icon="ni ni-lock-circle-open"
                  required v-model="user.password" :valid="$v.user.password.required && $v.user.password.minLength" :error="errorPassword" v-on:blur="dirtyPassword">
                </base-input>
                <textarea  class="form-control form-control-alternative" rows="3"
                  placeholder="Descrição sobre você ou sobre a empresa" v-model="user.description">
                </textarea>
                <v-select class="form-control form-control-alternative select"
                  required v-model="user.address.city" :options="cities" :valid="$v.user.address.city.required" :error="errorCity">
                </v-select>
                <input alternative type="file" class="form-control-file" placeholder="Foto"
                  ref="file" v-on:change="handleFileUpload()">
                <base-checkbox required v-model="terms">
                  <span>Eu concordo com os <a href="#">Termos de Política</a></span>
                </base-checkbox>
                <div class="text-center">
                  <button class="btn btn-primary my-4" type="submit">Criar uma conta</button>
                </div>
              </form>
            </template>
          </card>
        </div>
      </div>
    </div>
  </section>
</template>
<script>
import axios from "axios";
import { cities } from "../../assets/json/states.json";
import { required, minLength, email } from "vuelidate/lib/validators";

export default {
  name: "register",
  components: {},
  data() {
    return {
      cities,
      user: {
        is_company: false,
        address: {
          city: "Cidade"
        }
      },
      image: {},
      terms: false,
      loading: false
    };
  },
  methods: {
    dirtyName() {
      this.$v.user.name.$touch();
    },
    dirtyLastName() {
      this.$v.user.last_name.$touch();
    },
    dirtyEmail() {
      this.$v.user.email.$touch();
    },
    dirtyPassword() {
      this.$v.user.password.$touch();
    },
    dirtyCity() {
      this.$v.user.address.city.$touch();
    },
    dirtySite() {
      this.$v.user.site.$touch();
    },
    onSubmit() {
      this.loading = true;
      if (this.terms == true) {
        let formData = new FormData();
        formData.append("image", this.image);
        formData.append("data", JSON.stringify(this.user));

        axios
          .post("https://ceara-jobs-api-iic.herokuapp.com/user/save/", formData, {
            headers: {
              "Content-Type": "multipart/form-data"
            }
          })
          .then(() => {
            this.loading = false;
            this.$notify({
              group: "not-cea-job",
              title: "Sucesso!",
              text: "Cadastro realizado com sucesso!",
              position: "top right",
              type: "success"
            });
            this.$router.push({ path: "/login" });
          })
          .catch(err => {
            console.log(err.response);
            this.loading = false;
            this.$notify({
              group: "not-cea-job",
              title: "Erro!",
              text: "Infelizmente algum erro inesperado ocorreu :(",
              position: "top right",
              type: "error"
            });
          });
      } else {
        this.$notify({
          group: "not-cea-job",
          title: "Aviso!",
          text: "Para prosseguir, aceite os Termos de Política!",
          position: "top right",
          type: "warn"
        });
      }
    },
    handleFileUpload() {
      this.image = this.$refs.file.files[0];
    }
  },
  computed: {
    errorName() {
      if (this.$v.user.name.$dirty) {
        if (!this.$v.user.name.required && !this.$v.user.name.minLength) {
          return null;
        }
        if (!this.$v.user.name.required) {
          return "Campo obrigatório.";
        }
        if (!this.$v.user.name.minLength) {
          // eslint-disable-next-line
          return `Deve conter ao menos ${
            this.$v.user.name.$params.minLength.min
          } caracteres.`;
        }
      }
      return null;
    },
    errorLastName() {
      if (this.$v.user.last_name.$dirty) {
        if (!this.$v.user.last_name.required) {
          return "Campo obrigatório.";
        }
        if (!this.$v.user.last_name.minLength) {
          // eslint-disable-next-line
          return `Deve conter ao menos ${
            this.$v.user.last_name.$params.minLength.min
          } caracteres.`;
        }
      }
      return null;
    },
    errorEmail() {
      if (this.$v.user.email.$dirty) {
        if (!this.$v.user.email.required) {
          return "Campo obrigatório.";
        }
        if (!this.$v.user.email.email) {
          return "Formato de email inválido.";
        }
      }
      return null;
    },
    errorPassword() {
      if (this.$v.user.password.$dirty) {
        if (!this.$v.user.password.required) {
          return "Campo obrigatório.";
        }
        if (!this.$v.user.password.minLength) {
          return `Campo 'Senha' deve conter no mínimo ${
            this.$v.user.password.$params.minLength.min
          } caracteres.`;
        }
      }
      return null;
    },
    errorCity() {
      // eslint-disable-next-line
      if (!this.$v.user.address.city.required && this.$v.user.address.city.$model !== "Cidade") {
        return "Campo obrigatório.";
      }
    },
    errorSite() {
      if (!this.$v.user.site.minLength) {
        return `Campo 'Site' deve conter no mínimo ${
          this.$v.user.site.$params.minLength.min
        } caracteres.`;
      }
    }
  },
  validations: {
    user: {
      name: {
        required,
        minLength: minLength(2)
      },
      last_name: {
        required,
        minLength: minLength(3)
      },
      email: {
        required,
        email
      },
      password: {
        required,
        minLength: minLength(6)
      },
      address: {
        city: {
          required
        }
      },
      site: {
        minLength: minLength(7)
      }
    }
  }
};
</script>
<style scoped>
.button.btn.dropdown-toggle.btn-default {
  padding: 1.25rem !important;
}
</style>
