<template>
  <section class="section section-shaped section-lg my-0">
    <div class="shape shape-style-1 bg-gradient-default">
    </div>
    <div class="container pt-lg-md">
      <div class="row justify-content-center">
        <div class="col-lg-7">
          <card type="secondary" shadow header-classes="bg-white pb-6" body-classes="px-lg-6 py-lg-6" class="border-0">
            <template>
              <form role="form" @submit.prevent="onSubmit()">
                <base-input alternative class="mb-3" placeholder="Título da vaga" addon-left-icon="ni ni-hat-3" required
                  v-model="vacancy.title" :valid="$v.vacancy.title.required && $v.vacancy.title.minLength" :error="errorTitle">
                </base-input>
                <base-input alternative class="mb-3" placeholder="Email para contato" addon-left-icon="ni ni-email-83"
                  required v-model="vacancy.email" :valid="$v.vacancy.email.email" :error="errorEmail">
                </base-input>
                <base-input alternative class="mb-3" placeholder="Média salárial" addon-left-icon="ni ni-money-coins"
                  v-model="vacancy.avg_salary">
                </base-input>
                <textarea class="form-control form-control-alternative" rows="3" required
                  placeholder="Descrição sobre a vaga" v-model="vacancy.description">
                </textarea>
                <v-select class="form-control form-control-alternative select"
                  v-model="vacancy.type_hiring" :options="typesHiring" placeholder="Modalidade de contratação">
                </v-select>
                <v-select class="form-control form-control-alternative select"
                  v-model="vacancy.level_experience" :options="levelsExperience" placeholder="Experiência">
                </v-select>
                <v-select class="form-control form-control-alternative select mb-3"
                  v-model="vacancy.address.city" :options="cities" placeholder="Cidade">
                </v-select>
                <base-input alternative class="mb-3" placeholder="Endereço" addon-left-icon="ni ni-hat-3" required
                  v-model="vacancy.address.street" :error="errorAddress"
                  :valid="$v.vacancy.address.street.required && $v.vacancy.address.city.required">
                </base-input>
                <input alternative type="file" class="form-control-file" placeholder="Foto"
                  ref="file" v-on:change="handleFileUpload()">
                <base-checkbox required v-model="vacancy.remote">
                  <span>Vaga remota ou Home office?</span>
                </base-checkbox>
                <div class="text-center">
                  <button class="btn btn-primary my-4" type="submit">Cadastrar vaga</button>
                </div>
              </form>
            </template>
          </card>
        </div>
      </div>
    </div>
  </section>
</template>
<script>
import axios from "axios";
import { cities } from "../assets/json/states.json";
import { typesHiring } from "../assets/json/type-hiring.json";
import { required, minLength, email } from "vuelidate/lib/validators";
import { levelsExperience } from "../assets/json/level-experience.json";

const NUM_MIN_LETRAS_DESCRIPTION = 35;

export default {
  name: "registerVacancy",
  components: {},
  data() {
    return {
      cities,
      image: {},
      typesHiring,
      vacancy: {
        address: {}
      },
      loading: false,
      levelsExperience
    };
  },
  methods: {
    onSubmit() {
      this.loading = true;
      if (
        window.localStorage.getItem("token") != null &&
        this.vacancy.description != null &&
        this.vacancy.description.length >= NUM_MIN_LETRAS_DESCRIPTION
      ) {
        const token = window.localStorage.getItem("token");
        let formData = new FormData();
        formData.append("image", this.image);
        formData.append("data", JSON.stringify(this.vacancy));
        axios
          .post("https://ceara-jobs-api-iic.herokuapp.com/vacancy/save/", formData, {
            headers: {
              // eslint-disable-next-line
              "Authorization": "bearer " + token,
              "Content-Type": "multipart/form-data"
            }
          })
          .then(() => {
            this.loading = false;
            this.$notify({
              group: "not-cea-job",
              title: "Sucesso!",
              text: "Cadastro realizado com sucesso!",
              position: "top right",
              type: "success"
            });
            this.$router.push({ path: "/" });
          })
          .catch(() => {
            this.$notify({
              group: "not-cea-job",
              title: "Erro!",
              text: "Infelizmente algum erro inesperado ocorreu :(",
              position: "top right",
              type: "error"
            });
          });
      } else {
        this.$notify({
          group: "not-cea-job",
          title: "Aviso!",
          // eslint-disable-next-line
          text: "Você deve estar logado para poder cadastrar uma vaga e preencher os campos obrigatórios!",
          position: "top right",
          type: "warn"
        });
      }
    },
    handleFileUpload() {
      this.image = this.$refs.file.files[0];
    }
  },
  computed: {
    errorTitle() {
      if (!this.$v.vacancy.title.required) {
        return "Campo obrigatório.";
      }
      if (!this.$v.vacancy.title.minLength) {
        // eslint-disable-next-line
        return `Deve conter ao menos ${this.$v.vacancy.title.$params.minLength.min} letras.`;
      }
      return null;
    },
    errorEmail() {
      if (!this.$v.vacancy.email.required) {
        return "Campo obrigatório.";
      }
      if (!this.$v.vacancy.email.email) {
        return "Email inválido.";
      }
      return null;
    },
    errorAddress() {
      if (!this.$v.vacancy.address.city.required) {
        return "A cidade é obrigatória.";
      }
      if (!this.$v.vacancy.address.street.required) {
        return "O endereço é obrigatório.";
      }
      return null;
    }
  },
  validations: {
    vacancy: {
      title: {
        required,
        minLength: minLength(5)
      },
      email: {
        required,
        email
      },
      address: {
        city: {
          required
        },
        street: {
          required
        }
      }
    }
  }
};
</script>
<style>
.form-control-file {
  margin-top: 15px;
  margin-bottom: 10px;
}
</style>
